body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
div,
p {
  box-sizing: border-box;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
#body-box {
  width: 100%;
  height: 100%;
  overflow-y: auto;
}
#background {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../images/bg-5_840e12aea8d273c2c12ab5237182bed9.jpg);
  background-size: cover;
  background-attachment: fixed;
  -webkit-filter: blur(2px);
          filter: blur(2px);
}
#header-box {
  z-index: 20;
  height: 42px;
  width: 100%;
  position: fixed;
  background-color: hsla(0, 0%, 100%, 0.4);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}
#app-box {
  z-index: 10;
  margin: 0 auto;
  padding-top: 42px;
  width: 1160px;
}
#rotation-box {
  padding-top: 42px;
}
.header {
  position: relative;
  height: 100%;
  width: 1160px;
  margin: 0 auto;
}
.header .item:hover {
  background-color: hsla(0, 0%, 100%, 0.3);
}
.header .item:hover .icon {
  background-color: pink;
  border-radius: 50%;
  -webkit-transition: all 800ms;
  transition: all 800ms;
}
.header .to-home-btn {
  padding: 0 12px;
  display: inline-block;
  line-height: 42px;
  cursor: pointer;
}
.header .to-home-btn .ff-logo {
  display: inline-block;
  height: 20px;
  width: 20px;
  vertical-align: middle;
  background: url(../images/firefly_60040aac82d6543e9eaa8d2cd3b13286.png);
  background-size: 100% 100%;
  -webkit-transition: all 800ms;
  transition: all 800ms;
}
.header .to-home-btn .main-site {
  font-size: 16px;
  color: #222;
}
.header .header-right {
  display: inline-block;
  line-height: 42px;
  position: absolute;
  right: 0;
  height: 100%;
}
.header .header-right .upload-btn {
  display: inline-block;
  line-height: 42px;
  padding: 0 12px;
  cursor: pointer;
}
.header .header-right .upload-btn .upload-img {
  display: inline-block;
  line-height: 42px;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  margin-right: 12px;
  background-image: url(../images/upload_514bd8861206d6cdf41f4b4c843c0a51.png);
  background-size: 100% 100%;
}
.header .header-right .upload-btn .upload-title {
  line-height: 42px;
  font-size: 16px;
  color: #222;
}
.header .header-right .user {
  cursor: pointer;
}
.header .header-right .user .avatar {
  line-height: 42px;
  display: inline-block;
  width: 36px;
  height: 36px;
  vertical-align: middle;
  border-radius: 50%;
  background-color: #777;
  background-size: cover;
  background-position: center center;
}
.header .header-right .user .avatar.gray {
  background-image: url(../images/avatar-gray_aba20ee5096182a1678735c1e3eeb475.png);
}
.header .header-right .user .avatar .user-operate {
  position: absolute;
  top: 42px;
  overflow: hidden;
  right: -48px;
  height: 0;
  border-radius: 2px;
  background-color: hsla(0, 0%, 100%, 0.4);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 600ms;
  transition: all 600ms;
}
.header .header-right .user .avatar .user-operate li {
  line-height: 42px;
  padding: 0 12px;
}
.header .header-right .user .avatar .user-operate li .user-icon {
  display: inline-block;
  line-height: 42px;
  width: 16px;
  height: 16px;
  margin-right: 12px;
  vertical-align: middle;
  background-size: cover;
  background-position: center center;
}
.header .header-right .user .avatar .user-operate li .user-icon.name-icon {
  background-image: url(../images/user_7778111f98f65f5a33b1c746884e2280.png);
}
.header .header-right .user .avatar .user-operate li .user-icon.logout-icon {
  background-image: url(../images/logout_03c3f6d817c29277b0eb1a02440d9ad2.png);
}
.header .header-right .user .avatar .user-operate li:hover {
  background-color: hsla(0, 0%, 100%, 0.3);
  -webkit-transition: all 600ms;
  transition: all 600ms;
}
.header .header-right .user .avatar:hover .user-operate {
  height: 84px;
  -webkit-transition: all 600ms;
  transition: all 600ms;
}
