.video-nav-box {
  position: fixed;
  top: 500px;
  right: 50px;
  width: 60px;
  background-color: #f6f9fa;
  border-radius: 2px;
  border: 1px solid #e5e9ef;
}
.video-nav-box .nav-li {
  cursor: pointer;
  text-align: center;
  color: black;
  line-height: 2;
  font-size: 16px;
}
.video-nav-box .nav-li.selected,
.video-nav-box .nav-li:hover {
  color: white;
  background-color: #3fa9f5;
}
.video-nav-box .to-top {
  cursor: pointer;
  border-top: 1px solid #e5e9ef;
  width: 60px;
  height: 60px;
  background-image: url(data:image/png;base64,);
  background-position: center center;
  background-size: 40px;
  background-repeat: no-repeat;
}
.video-nav-box .to-top:hover {
  background-color: #3fa9f5;
  background-image: url(data:image/png;base64,);
}
