.login-bg {
  z-index: 20;
  position: fixed;
  display: -webkit-box;
  display: flex;
  background: rgba(0, 0, 0, 0.6);
  top: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  -webkit-box-align: center;
          align-items: center;
  -webkit-box-pack: center;
          justify-content: center;
}
.login-bg .login-box {
  position: relative;
  background-color: white;
  border-radius: 8px;
  width: 400px;
  padding: 48px;
}
.login-bg .login-box .close-btn {
  position: absolute;
  display: block;
  height: 28px;
  width: 28px;
  top: 15px;
  right: 15px;
  cursor: pointer;
  background-image: url(data:image/png;base64,);
  background-size: cover;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: all 400ms;
  transition: all 400ms;
}
.login-bg .login-box .close-btn:hover {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-transition: all 400ms;
  transition: all 400ms;
}
.login-bg .login-box nav.form-title {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
          flex-flow: row nowrap;
  justify-content: space-around;
  margin-bottom: 30px;
  text-align: center;
  font-size: 22px;
  color: #333;
}
.login-bg .login-box nav.form-title span {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  width: 120px;
  height: 34px;
  line-height: 22px;
  cursor: pointer;
  padding: 5px;
  border-bottom: 0;
  font-size: 16px;
  color: #9e9e9e;
  -webkit-transition: all 400ms;
  transition: all 400ms;
}
.login-bg .login-box nav.form-title span.selected {
  color: #333;
  font-size: 18px;
}
.login-bg .login-box nav.form-title span.selected::after {
  width: 100%;
}
.login-bg .login-box nav.form-title span::after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 0;
  background-color: #3fa9f5;
  -webkit-transition: all 400ms;
  transition: all 400ms;
}
.login-bg .login-box .form-view {
  width: 304px;
  overflow-x: hidden;
}
.login-bg .login-box .form-view .login-register {
  position: relative;
  width: 630px;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
          flex-flow: row nowrap;
  -webkit-box-pack: justify;
          justify-content: space-between;
}
.login-bg .login-box .form-view .login-register.view-login {
  right: 0;
  -webkit-transition: all 400ms;
  transition: all 400ms;
}
.login-bg .login-box .form-view .login-register.view-register {
  right: 326px;
  -webkit-transition: all 400ms;
  transition: all 400ms;
}
.login-bg .login-box .form-view .divider {
  height: 172px;
  width: 1px;
  background-color: #3fa9f5;
}
.login-bg .login-box .form-view .login,
.login-bg .login-box .form-view .register {
  width: 304px;
}
.login-bg .login-box .form-view .login .input,
.login-bg .login-box .form-view .register .input {
  display: inline-block;
  line-height: 40px;
  margin-bottom: 10px;
  width: 100%;
  outline: none;
  font-size: 16px;
  border: none;
  box-shadow: none;
  border-bottom: 1px solid #3fa9f5;
}
.login-bg .login-box .form-view .login .submit-btn,
.login-bg .login-box .form-view .register .submit-btn {
  width: 100%;
  cursor: pointer;
  margin-top: 24px;
  height: 42px;
  line-height: 42px;
  border-radius: 21px;
  background-color: #3fa9f5;
  color: #fff;
  font-size: 16px;
  text-align: center;
}
.login-bg .login-box .form-view .register .password-box {
  width: 100%;
  position: relative;
}
.login-bg .login-box .form-view .register .password-box .password-eyes {
  display: inline-block;
  position: absolute;
  height: 22px;
  width: 22px;
  line-height: 42px;
  top: 0;
  right: 0;
  margin: 10px;
  background-size: 100% 100%;
  cursor: pointer;
}
.login-bg .login-box .form-view .register .password-box.hide-password .password-eyes {
  background-image: url(../images/show-psd_9402444e00036c51ee93ed4f84f825cf.png);
}
.login-bg .login-box .form-view .register .password-box.show-password .password-eyes {
  background-image: url(../images/hide-psd_69d64da85304e95023ba921ec0eb47d8.png);
}
