.type-box {
  padding-bottom: 40px;
}
.type-box h2 {
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 15px;
  border-bottom: 1px solid #3fa9f5;
}
.type-box h2 .type-icon {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-size: 100% 100%;
  margin-right: 12px;
  vertical-align: bottom;
}
.type-box h2 .type-title {
  vertical-align: text-bottom;
}
.type-box h2.movie-h .type-icon {
  background-image: url(../images/movie_2424ad363119417e455ab09ee300d7ad.png);
}
.type-box h2.anime-h .type-icon {
  width: 40px;
  height: 40px;
  background-image: url(../images/anime_73c8dadb179e1087303977cb9752c1ad.png);
}
.type-box h2.music-h .type-icon {
  background-image: url(../images/music_47ba83acd58945f9ee30403b3f64bb28.png);
}
.type-box h2.entertainment-h .type-icon {
  background-image: url(../images/entertainment_f159df48acd212f9750cf49151d678dc.png);
}
.type-box h2.study-h .type-icon {
  background-image: url(../images/study_01d672ab04173ee8644e044e4afd2cc6.png);
}
.type-box h2.science-h .type-icon {
  background-image: url(data:image/png;base64,);
}
.type-box h2.life-h .type-icon {
  background-image: url(../images/life_64cd083f42180c4e3fc57b5d2fd3e89e.png);
}
.type-box h2.other-h .type-icon {
  background-image: url(../images/other_2916821a4802d59adc317995a299013f.png);
}
.type-box .video-ul {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
          flex-flow: row wrap;
}
.type-box .video-ul .video-li {
  width: 320px;
  margin-right: 30px;
}
.type-box .video-ul .video-li .cover {
  width: 100%;
  height: 200px;
  border-radius: 8px;
  position: relative;
  overflow: hidden;
  background-image: url(data:image/png;base64,);
  background-position: center center;
  background-size: 80px 60px;
  background-repeat: no-repeat;
}
.type-box .video-ul .video-li .cover:hover .bottom-author {
  bottom: 0px;
  -webkit-transition: bottom 400ms;
  transition: bottom 400ms;
}
.type-box .video-ul .video-li .cover .type-tag {
  position: absolute;
  top: 10px;
  right: 10px;
  display: block;
  font-size: 12px;
  line-height: 18px;
  padding: 0 5px;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
}
.type-box .video-ul .video-li .cover .bottom-author {
  position: absolute;
  padding: 10px 10px 0 10px;
  width: 320px;
  bottom: -38px;
  -webkit-transition: bottom 3y00ms;
  transition: bottom 3y00ms;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0)));
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.type-box .video-ul .video-li .cover .bottom-author .author {
  color: white;
  font-size: 14px;
  line-height: 2;
}
.type-box .video-ul .video-li .cover .bottom-author .view-count {
  position: absolute;
  right: 10px;
  color: white;
  font-size: 14px;
  line-height: 2;
}
.type-box .video-ul .video-li .cover .bottom-author .view-count::before {
  position: absolute;
  display: inline-block;
  content: "";
  width: 20px;
  height: 20px;
  left: -25px;
  background-image: url(../images/hot_86da0705c97c69410fa1b64d722c4c49.png);
  background-size: 100% 100%;
}
.type-box .video-ul .video-li .bottom-message {
  width: 100%;
}
.type-box .video-ul .video-li .bottom-message .video-name {
  margin: 16px 0 0;
  font-size: 16px;
  color: black !important;
}
.type-box .video-ul .video-li .bottom-message .update-message {
  color: #9ba3ab;
  font-size: 14px;
}
.type-box .no-type-video {
  height: 300px;
  width: 100%;
  background-image: url(../images/empty-bg_ba71286080558e2dad56e73733b18be8.png);
  background-size: cover;
}
.type-box .no-type-video p {
  margin-top: 50px;
  text-align: center;
  font-size: 20px;
  color: rgba(0, 0, 0, 0.4);
}
