#global-alert {
  z-index: 100;
  display: block;
  border-radius: 4px;
  padding: 0 22px;
  position: fixed;
  font-size: 20px;
  line-height: 2.5;
  top: -54px;
  -webkit-transition: top 1s ease-out;
  transition: top 1s ease-out;
  opacity: 0.4;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  color: white;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}
#global-alert.show {
  -webkit-transition: all 1s ease-in;
  transition: all 1s ease-in;
  top: 30px;
  opacity: 1;
  -webkit-animation: in 1s ease-in;
          animation: in 1s ease-in;
}
@-webkit-keyframes in {
  0% {
    top: -54px;
  }
  100% {
    top: 30px;
  }
}
@keyframes in {
  0% {
    top: -54px;
  }
  100% {
    top: 30px;
  }
}
#global-alert.success {
  background-color: rgba(51, 238, 176, 0.8);
}
#global-alert.error {
  background-color: rgba(240, 47, 127, 0.8);
}
#global-alert.notice {
  background-color: rgba(63, 169, 245, 0.8);
}
