.upload-video-bg {
  z-index: 20;
  position: fixed;
  display: -webkit-box;
  display: flex;
  background: rgba(0, 0, 0, 0.6);
  top: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  -webkit-box-align: center;
          align-items: center;
  -webkit-box-pack: center;
          justify-content: center;
}
.upload-video-bg .upload-box {
  position: relative;
  background-color: white;
  border-radius: 8px;
  width: 400px;
  padding: 48px;
}
.upload-video-bg .upload-box .close-btn {
  position: absolute;
  display: block;
  height: 28px;
  width: 28px;
  top: 15px;
  right: 15px;
  cursor: pointer;
  background-image: url(data:image/png;base64,);
  background-size: cover;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: all 400ms;
  transition: all 400ms;
}
.upload-video-bg .upload-box .close-btn:hover {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-transition: all 400ms;
  transition: all 400ms;
}
.upload-video-bg .upload-box nav.form-title {
  margin-bottom: 30px;
  text-align: center;
  font-size: 22px;
  color: #333;
  border-bottom: 2px solid #3fa9f5;
}
.upload-video-bg .upload-box .form-view {
  overflow: hidden;
  -webkit-transition: all 500ms;
  transition: all 500ms;
  height: 266px;
}
.upload-video-bg .upload-box .form-view.hide {
  height: 0;
  -webkit-transition: all 500ms;
  transition: all 500ms;
}
.upload-video-bg .upload-box .form-view .form-box ul li {
  margin-bottom: 10px;
  height: 40px;
  width: 100%;
}
.upload-video-bg .upload-box .form-view .form-box ul li input,
.upload-video-bg .upload-box .form-view .form-box ul li select {
  font-size: 16px;
  display: block;
  height: 100%;
  width: 100%;
  margin: 0;
  outline: none;
  border: none;
  box-shadow: none;
  border-bottom: 1px solid #3fa9f5;
}
.upload-video-bg .upload-box .form-view .form-box ul li input option,
.upload-video-bg .upload-box .form-view .form-box ul li select option {
  display: block;
  height: 32px;
  line-height: 32px;
  font-size: 16px;
}
.upload-video-bg .upload-box .form-view .drag-box {
  height: 100px;
  border-radius: 6px;
  background-image: url(../images/empty-bg_ba71286080558e2dad56e73733b18be8.png);
  background-size: cover;
  border: 2px dashed rgba(79, 157, 230, 0.8);
}
.upload-video-bg .upload-box .form-view .drag-box p {
  margin-top: 12px;
  font-size: 16px;
  text-align: center;
  color: rgba(0, 0, 0, 0.6);
}
.upload-video-bg .upload-box .form-view .drag-box.has-video {
  padding: 20px;
}
.upload-video-bg .upload-box .form-view .drag-box.has-video .file-icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-size: 100% 100%;
  background-image: url(../images/video-file_e9455b9a5ef638f4d5f4e164e2ee11e9.png);
}
.upload-video-bg .upload-box .form-view .drag-box.has-video .file-text {
  font-size: 16px;
}
.upload-video-bg .upload-box .form-view .upload-submit-btn {
  height: 42px;
  margin-top: 24px;
  color: white;
  font-size: 16px;
  text-align: center;
  line-height: 42px;
  border-radius: 21px;
  background-color: #3fa9f5;
  cursor: pointer;
}
.upload-video-bg .upload-box .while-update {
  height: 266px;
  overflow: hidden;
  -webkit-transition: all 500ms;
  transition: all 500ms;
}
.upload-video-bg .upload-box .while-update.hide {
  height: 0;
  -webkit-transition: all 500ms;
  transition: all 500ms;
}
.upload-video-bg .upload-box .while-update .load-animation {
  height: 206px;
  background-image: url(../images/loading-1_7eb41c5abbb289750c85a8cc03d02266.gif);
  background-position: center center;
  background-repeat: no-repeat;
  margin-bottom: 20px;
}
.upload-video-bg .upload-box .while-update #progress-box .nanobar {
  height: 40px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 20px;
}
.upload-video-bg .upload-box .while-update #progress-box .nanobar .bar {
  border-radius: 20px;
  background: url(data:image/gif;base64,) 100%;
}
